<template>
<article>
    <header>
        <a target="_new" href="https://github.com/d3/d3">d3</a> / <a target="_new" href="https://github.com/vuejs/vue">vue</a> / <a target="_new" href="https://github.com/any86/any-touch">any-touch</a>
    </header>
        
        <svg xmlns="http://www.w3.org/2000/svg" width="1000" height="1000" style="width:100%">
    <g transform="translate(100, 100)">
        <path
            v-for="(linkPath, index) in linkPaths"
            :key="linkPath+index"
            :d="linkPath"
            class="line"
        />
    </g>

    <g transform="translate(100, 100)">
        <foreignObject
            v-for="(node,index) in nodes"
            v-show="!node.hidden"
            :class="{[`at-${action}`]:activeNode===node}"
            :key="'foreignObject'+index"
            :width="itemWidth"
            :height="itemHeight"
            :x="node.y - itemWidth/2"
            :y="node.x - itemHeight/2"
            @panstart="onPanstart(index,$event)"
            @panmove="onPanmove(index,$event)"
            @panend="onPanend"
            @pancancel="onPanend"
            @tap="onTap(index)"
        >
            <body xmlns="http://www.w3.org/1999/xhtml">
                <div class="text">
                    <p>: {{node.depth}}</p>
                    <p>: {{index}}</p>
                </div>
            </body>
        </foreignObject>
    </g>
</svg>
        </article>

</template>

<script>
import * as d3 from 'd3';
// import Core from '../../../packages/core/dist/index.es.js';
// import Pan from '../../../packages/pan/dist/index.es.js';
// Core.use(Pan, { threshold: 0 });

import AnyTouch from '../../../packages/any-touch/dist/any-touch.umd';

import { treeChart, dataset, animate } from './topology';
const pointsGroup = [];
const paths = [];
export default {
    name: 'Topology',

    props: {
        itemWidth: {
            type: Number,
            default: 160
        },
        itemHeight: {
            type: Number,
            default: 100
        }
    },

    data() {
        return {
            nodes: [],
            linkPaths: [],
            tree: {},
            activeNode: null,
            action: ''
        };
    },
    mounted() {
        this.renderTree();
        const at = new AnyTouch(this.$el);
        this.$on('hook:destroy', at.destroy);
    },
    methods: {
        /**
         * d3
         */
        genTreeData(data) {
            const width = 1000;
            const height = 1000;
            const root = d3.hierarchy(data);
            root.descendants().forEach((node) => {
                node._children = node.children;
                node.hidden = false;
            });
            root.dx = 110;
            root.dy = width / (root.height + 1);
            return d3
                .tree()
                .separation(function(a, b) {
                    return (a.parent == b.parent ? 2 : 1) / a.depth;
                })
                .nodeSize([root.dx, root.dy])(root);
        },

        renderTree() {
            this.tree = this.genTreeData(dataset);
            this.updateLinks();
            this.updateNodes();
        },

        updateNodes() {
            this.nodes = this.tree.descendants();
        },

        /**
         * 
         */
        updateLinks() {
            this.linkPaths = this.tree.links().map((link) => {
                return d3
                    .linkHorizontal()
                    .x((d) => d.y)
                    .y((d) => d.x)(link);
            });
        },

        onPanstart(index, e) {
            const [item] = this.nodes.splice(index, 1);
            this.nodes.push(item);
            this.activeNode = item;
        },

        onPanmove(index, e) {
            this.action = e.type;
            const { deltaX, deltaY } = e;
            const { length } = this.nodes;
            this.activeNode.x += deltaY;
            this.activeNode.y += deltaX;
            this.updateLinks();
        },

        onPanend() {
            this.activeNode = null;
        },

        onTap(index) {
            // this.nodes.push()
            this.activeNode = this.nodes[index];

            if (void 0 === this.activeNode.collapse) {
                this.$set(this.activeNode, 'collapse', false);
            } else {
                this.activeNode.collapse = !this.activeNode.collapse;
            }

            const { x, y } = this.activeNode;
            const [a,...childNodes] = this.activeNode.descendants();
            // this.activeNode.children = this.activeNode.children ? null : this.activeNode._children;
            console.log(childNodes);

            childNodes.forEach((node) => {
                if (this.activeNode.collapse) {
                    node.hidden = false;
                    animate(0, 1, 200, (value) => {
                        node.x = x+(node._x-x) * value;
                        node.y = y+(node._y-y) * value;

                        this.updateLinks();
                    });
                } else {
                    const x1 = node.x;
                    const y1 = node.y;
                    node._x = x1;
                    node._y = y1;
                    animate(1, 0, 200, (value, isDone) => {
                        node.x = x - (x - x1) * value;
                        node.y = y - (y - y1) * value;
                        if (isDone) {
                            node.hidden = true;
                        }
                        this.updateLinks();
                    });
                }
            });

            this.updateNodes();
            this.updateLinks();
        }
    }
};
</script>

<style scope lang="scss">
header{
    position:fixed;
    width:100%;
    top:0;
    left:0;
    padding:16px;
    a{
        color:#69c;
    }
}
.line {
    fill: none;
    stroke: rgb(3, 159, 107);
    stroke-width: 1;
}
.circle {
    fill: rgb(3, 159, 107);
}

.text {
    background-color: rgb(3, 159, 107);
    margin: 8px;
    width: 90%;
    padding: 8px;
    text-align: center;
    color: #fff;
    box-shadow: 1px 2px 8px rgba(4, 108, 73, 0.5);
    border-radius: 4px;
    cursor: pointer;
}

foreignObject.at-panmove .text {
    cursor: move;

    background-color: rgb(3, 159, 107, 0.7);
}
</style>